import './rightbar.css';
import Online from "../online/Online";
import {Users} from "../../dummyData";
import { useContext, useEffect, useState } from 'react';
import {axiosInstance} from '../../config';
import { Link } from 'react-router-dom';
import { AuthContext } from '../../context/AuthContext';
import {Add, Remove} from "@material-ui/icons";

export default function Rightbar({user}) {
    const PF = process.env.REACT_APP_PUBLIC_FOLDER;
    const [friends, setFriends] = useState([]);
    const {user:currentUser, dispatch} = useContext(AuthContext);
    const [followed, setFollowed] = useState(false);
    
    useEffect(()=>{
        const check = currentUser.following.includes(user?._id);
        setFollowed(check);
    },[currentUser,user]);

    useEffect(()=>{
        const getFriends = async () =>{
            try{
                const friendList = await axiosInstance.get("/users/friends/" + user._id);
                setFriends(friendList.data);
            }catch(err){    
                console.log(err);
            }
        };
        getFriends();
    },[user]);

    const handleClick=async ()=>{
        try{
            if(followed){
                await axiosInstance.post(`/users/${user._id}/unfollow`, {
                  userId: currentUser._id,
                });
                dispatch({ type: "UNFOLLOW", payload: user._id });
            }else{
                await axiosInstance.post(`/users/${user._id}/follow`, {
                  userId: currentUser._id,
                });
                dispatch({ type: "FOLLOW", payload: user._id });
            }
            setFollowed(!followed);
        }catch(err){
            console.log(err);
        }
    }

    const HomeRightbar =() =>{
        return (
            <>
                <div className="birthdayContainer">
                    <img src={`${PF}gift.png`} alt="" className="birthdayImg" />
                    <span className="birthdayText"><b>Pola Foster</b> and <b>3 other friends</b> have birthday today.</span>
                </div>
                <img src={`${PF}/ad.png`} alt="" className="rightbarAd" />
                <h4 className="rightbarTitle">Online Friends</h4>
                <ul className="rightbarFriendsList">
                    {Users.map(user => (
                        <Online key={user.id} user={user}/>
                    ))}
                </ul>
            </>
        );
    }

    const ProfileRightbar =()=>{
        return (
          <>
          {user.username !== currentUser.username && (
              <button className="rightbarFollowButton" onClick={handleClick}>
                  {followed ? "Unfollow" : "Follow"}
                  {followed ? <Remove/> : <Add/>}
              </button>
          )}
            <h4 className="rightbarTitle">User information</h4>
            <div className="rightbarInfo">
              <div className="rightbarInfoItem">
                <span className="rightbarInfoKey">City: </span>
                <span className="rightbarInfoValue">
                  {user.city || "Not Announced"}
                </span>
              </div>
              <div className="rightbarInfoItem">
                <span className="rightbarInfoKey">From: </span>
                <span className="rightbarInfoValue">
                  {user.from || "Not Announced"}
                </span>
              </div>
              <div className="rightbarInfoItem">
                <span className="rightbarInfoKey">Relationship: </span>
                <span className="rightbarInfoValue">
                  {user.relationship === 1
                    ? "Single"
                    : user.relationship === 2
                    ? "Commited"
                    : "Not Announced"}
                </span>
              </div>
            </div>
            <h4 className="rightbarTitle">User friends</h4>
            <div className="rightbarFollowings">
              {friends.map((friend) => (
                <Link key={friend._id} to={"/profile/"+friend.username} style={{textDecoration: "none"}}>
                  <div className="rightbarFollowing">
                        <img
                            src={
                                friend.profilePicture
                                ? PF + friend.profilePicture
                                : PF + "person/noAvatar.png"
                            }
                            alt=""
                            className="rightbarFollowingImg"
                            />
                        <span className="rightbarFollowingName">{friend.username}</span>
                    </div>
                </Link>
              ))}
            </div>
          </>
        );
    }

    return (
        <div className="rightbar">
            <div className="rightbarWrapper">
                {user ? <ProfileRightbar/> : <HomeRightbar />}
            </div>
        </div>
    )
}
